.brk-highlite-primary-1 {
  position: relative;
  color: var(--brand-primary) !important;
  margin-left: 7px;

  &:after {
    content: '';
    position: absolute;
    left: -6px;
    top: 1px;
    width: calc(100% + 15px);
    height: calc(100% - 2px);
    background-color: rgba(var(--brand-primary-rgb),.3);
  }
}

.brk-highlite-primary-2 {
  text-transform: uppercase;
  position: relative;

  &:hover {
    color: #fff;
  }

  &::after {
    content: '';

    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    background-color: var(--brk-base-5);
  }
  
}

.brk-cta {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 1000;
  
  &__close {
    position: absolute;
    right: 100px;
    top: 50%;

    width: 30px;
    height: 30px;

    transform: translateY(-50%);
    cursor: pointer;

    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 14px;
      height: 2px;
      top: 50%;
      left: 50%;
      display: inline-block;
      transform-origin: center center;
      background-color: var(--brand-primary);
      transition: width .3s;
    }

    &:before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }

    &_white {
      &:after,
      &:before {
        background-color: #fff;
      }
    }
  }
}

.brk-cta-1 {
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.08);

  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 64px;

    @media screen and (max-width: 1230px){ 
      flex-direction: column;
      text-align: center;
    }
  }
}

.brk-cta-2 {

  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 12px 0;
  }

  &__divider {
    margin-left: 25px;
    margin-right: 25px;
    width: 1px;
    height: 40px;
    background-image: linear-gradient(to bottom, rgba(256,256,256, .01) 0%, rgba(256,256,256, .16) 50%, rgba(256,256,256, .01) 100%);
    
    @media screen and (max-width: 768px){ 
      display: block;
      width: 100%;
      height: 1px;
      margin: 15px 0;
    }
  }
}

.brk-cta-3 {
  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 15px 0;
    min-height: 64px;
  }
}

.brk-cta-4 {
  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 15px 0;
    min-height: 64px;
  }
}
